export const required = value =>{
    if(value) return undefined;
    return "Field is required!"
}
export const maxLengthCreator = (maxLength) => value =>{
    if(value && value.length > maxLength ) return `Length is over ${maxLength} symbols`;
    return undefined
}
export const minLength2 = value =>{
    if(value && value.length > 30 ) return 'Length is over 30 symbols';
    return undefined
}